import wxParse from 'mpvue-wxparse';

export default {
	data() {
		return {
			post:[],
			strings:'',
		};
	},
	components: {
		wxParse
	},
	onLoad(e) {
		console.log(e);
		uni.showLoading({
			title:'加载中...'
		})
		uni.request({
			url: 'https://laravelcode.info/api/posts/'+ e.postid,
			method: 'GET',
			data: {},
			success: res => {
				console.log(res.data);
				this.post = res.data;
				this.strings = "res.data.body";
				uni.hideLoading();
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {

	},
}
